.back-styles {
	height: 100vh;
	font-family: Goudy Old Style;
}

.bottom-container {
	margin-top: 250px;
}

.bottom-header {
	font-size: 30px;
}

@media screen and (max-width: 700px) {

	.main-header {
		font-size: 80px;
	}

	.bottom-header {
		font-size: 20px;
	}
}

@media screen and (max-width: 430px) {

	.main-header {
		font-size: 45px;
		padding-top: 20px ;
	}

	.bottom-container {
		margin-top: 150px;
	}

	.bottom-header {
		font-size: 12px;
	}

	.back-styles {
		height: 70vh;
		font-family: Goudy Old Style;
	}

	.heart-image {
		width: 30px;
		height: 30px;
	}
}

.main-header {
	font-size: 140px;
}