.card-styles {
	font-family: Goudy Old Style;
}

.main-container {
	display: flex;
}

.author-text {
	font-size: 30px;
}

.like-container {
	margin-top: 30px;
}

@media screen and (max-width: 600px) {
	.image{
		height: 170px;
		width: 250px;
	}

	.main-container {
		display: flex;
		flex-direction: column;
	}

	.author-text {
		font-size: 20px;
	}

	.like-container {
		margin-top: 5px;
	}
}