body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-bar {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.link {
	margin-left: 20px;
}

.contact-link {
	margin-left: auto;
}

@media screen and (max-width: 550px) {

	.nav-bar {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	.link {
		margin-left: 10px;
		padding: 10px;
		font-size: 15px;
		font-weight: 700px;
	}
}
.back-styles {
	height: 100vh;
	font-family: Goudy Old Style;
}

.bottom-container {
	margin-top: 250px;
}

.bottom-header {
	font-size: 30px;
}

@media screen and (max-width: 700px) {

	.main-header {
		font-size: 80px;
	}

	.bottom-header {
		font-size: 20px;
	}
}

@media screen and (max-width: 430px) {

	.main-header {
		font-size: 45px;
		padding-top: 20px ;
	}

	.bottom-container {
		margin-top: 150px;
	}

	.bottom-header {
		font-size: 12px;
	}

	.back-styles {
		height: 70vh;
		font-family: Goudy Old Style;
	}

	.heart-image {
		width: 30px;
		height: 30px;
	}
}

.main-header {
	font-size: 140px;
}
.card-styles {
	font-family: Goudy Old Style;
}

.main-container {
	display: -webkit-flex;
	display: flex;
}

.author-text {
	font-size: 30px;
}

.like-container {
	margin-top: 30px;
}

@media screen and (max-width: 600px) {
	.image{
		height: 170px;
		width: 250px;
	}

	.main-container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	.author-text {
		font-size: 20px;
	}

	.like-container {
		margin-top: 5px;
	}
}
.card-styles {
	font-family: Goudy Old Style;
}

.main-header {
	font-size: 50px;
}

@media screen and (max-width: 600px) {

	.main-header {
		font-size: 25px;
	}

	.img {
		width: 140px;
		height: 140px;
	}
}
