.nav-bar {
	display: flex;
	flex-wrap: wrap;
}

.link {
	margin-left: 20px;
}

.contact-link {
	margin-left: auto;
}

@media screen and (max-width: 550px) {

	.nav-bar {
		display: flex;
		flex-direction: column;
	}

	.link {
		margin-left: 10px;
		padding: 10px;
		font-size: 15px;
		font-weight: 700px;
	}
}